/**
 * @module ngeo.statemanager.module
*/
import ngeoBase from 'ngeo';
import ngeoStatemanagerLocation from 'ngeo/statemanager/Location';
import ngeoStatemanagerService from 'ngeo/statemanager/Service';

/**
 * @type {!angular.Module}
 */
const exports = angular.module('ngeoStatemanagerModule', [
  ngeoBase.module.name, // Change me when all dependencies are in a module.
  ngeoStatemanagerLocation.module.name,
  ngeoStatemanagerService.module.name
]);


export default exports;



